import { Component, OnInit } from '@angular/core'; import { Router } from '@angular/router';  @Component({   moduleId: module.id,   selector: 'veiculo-dados',   templateUrl: 'veiculo_dados.component.html' }) export class VeiculoDadosComponent implements OnInit {       displayMode: VeiculoDadosDisplayModeEnum;     displayModeEnum = VeiculoDadosDisplayModeEnum;       constructor(private router: Router) { }      ngOnInit() {       const path = this.router.url.split('/')[3];       switch (path) {         case 'details':           this.displayMode = VeiculoDadosDisplayModeEnum.Details;           break;         case 'info':           this.displayMode = VeiculoDadosDisplayModeEnum.Orders;           break;         case 'edit':           this.displayMode = VeiculoDadosDisplayModeEnum.Edit;           break;       }     }  }  enum VeiculoDadosDisplayModeEnum {   Details=0,   Orders=1,   Edit=2 }


