import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Veiculo } from '../../../modelo/Veiculo'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'veiculo-dados-details',   templateUrl: 'veiculo_dadosDetails.component.html' }) export class VeiculoDadosDetailsComponent implements OnInit {    veiculo: Veiculo;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.veiculo.Veiculo";                 let id = +params['id'];         this.veiculo = new Veiculo();         this.veiculo.cd_veiculo = id;         o.objetoJson = this.veiculo;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a veiculo. '; }      this.veiculo = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


